<template>
  <div>
    <div ref="editor"></div>
  </div>
</template>

<script>
  import E from 'wangeditor';

  export default {
    props: ['content'],
    data () {
      return {
        editor: null,
      }
    },
    mounted () {
      let editor = new E(this.$refs.editor);
      // 
      editor.customConfig.menus = [
        'head',  // 
        'bold',  // 
        'fontSize',  // 
        'fontName',  // 
        'italic',  // 
        'foreColor',  // 
        'backColor',  // 
        'link',  // 
        'list',  // 
        'justify',  // 
        'quote',  // 
        'emoticon',  // 
        'image',  // 
        'table',  // 
        'code',  // 
        'undo',  // 
        'redo'  // 
      ];
      // 
      editor.customConfig.pasteIgnoreImg = true;
      // onchange 
      editor.customConfig.onchangeTimeout = 500;
      //  5 
      editor.customConfig.uploadImgMaxLength = 5;
      // 
      editor.customConfig.onchange = (html) => {
        this.$emit('getEditorContent', html);
      };
      editor.create();
      this.editor = editor;
    },
    watch: {
      content (content) {
        this.editor.txt.html(content);
      }
    }
  }
</script>

<style scoped>
  h1, h2 {
    font-weight: normal;
  }

  ul {
    list-style-type: none;
    padding: 0;
  }

  li {
    display: inline-block;
    margin: 0 10px;
  }

  a {
    color: #42b983;
  }
</style>



